.iimagenesContainer {
  width: 70%;
  margin: auto;
  margin-top: 2%;
}

.iimagenesContainer .imagenesPage {
  width: 100%;
}

.iimagenesContainer .imagenesPage .imagen {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer {
  padding: 0;
  height: auto;
  width: 30%;
  position: relative;
  margin: auto 1%;
  overflow: hidden;
  cursor: pointer;
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer img {
  z-index: -1;
  position: inherit;
  width: 100%;
  transition: all 300ms ease-in-out;
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer .infoImage {
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  bottom: -40px;
  height: 40px;
  transition: all 300ms ease-in-out;
  display: flex;
  justify-content: space-around;
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer .infoImage p {
  color: #ffff;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer:hover img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.iimagenesContainer .imagenesPage .imagen .imagenContainer:hover .infoImage {
