.spinner {
  width: 10%;
  height: 90px;
  background-color: #18cab4;

  margin: 100px auto;
  -webkit-animation: sk-rotateplane 1.2s infinite ease-in-out;
  animation: sk-rotateplane 1.2s infinite ease-in-out;
}

@-webkit-keyframes sk-rotateplane {
  0% {
    -webkit-transform: perspective(120px);
  }
  50% {
    -webkit-transform: perspective(120px) rotateY(180deg);
  }
  100% {
    -webkit-transform: perspective(120px) rotateY(180deg) rotateX(180deg);
  }
}

@keyframes sk-rotateplane {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

.portadaContainer {
  padding: 12% 0;
  position: relative;
}

.portadaContainer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url(/Picture-bay/static/media/flowers-5080164_1920.d4b1277e.jpg);
  background-size: cover;
  background-position: top center;
  z-index: -1;
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
}

.portadaContainer .frase {
  text-align: center;
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 50px;
  line-height: 76px;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 45px;
  color: #ffff;
}

.portadaContainer .buscador {
  display: flex;
  justify-content: center;
  height: 50px;
}

.portadaContainer .buscador input,
.portadaContainer .buscador button {
  height: inherit;
  border: none;
  outline: none;
}

.portadaContainer .buscador input[type="text"] {
  width: 40%;
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  background-color: #ffff;
  padding: 0 2%;
}

.portadaContainer .buscador button[type="submit"] {
  width: 5%;
  cursor: pointer;
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
  background-color: #18cab4;
  color: #ffff;
  font-size: 20px;
}

@media screen and (max-width: 1026px) {
  .portadaContainer .frase {
    font-size: 36px;
    line-height: 43px;
    margin-bottom: 35px;
  }
}

@media screen and (max-width: 769px) {
  .portadaContainer .frase {
    width: 90%;
    margin-top: 5%;
  }
  .portadaContainer .buscador input[type="text"] {
    width: 60%;
  }
  .portadaContainer .buscador button[type="submit"] {
    width: 10%;
  }
}

@media screen and (max-width: 400px) {
  .portadaContainer .frase {
    font-size: 18px;
    line-height: 21px;
    margin: 15% auto 10%;
  }
  .portadaContainer .buscador input[type="text"] {
    width: 70%;
  }
  .portadaContainer .buscador button[type="submit"] {
    width: 15%;
  }
}
